<template>
  <div>
    <div id="top">
      <div>
        <span>微信号</span>
        <el-input
          v-model="searchName"
          placeholder="请输入"
          size="mini"
          class="search"
        ></el-input>

        <span>关联书籍</span>
        <el-input
          v-model="searchName"
          placeholder="请输入"
          size="mini"
          class="search"
        ></el-input>

        <span style="margin-right: 10px">打卡日期</span>
        <el-date-picker
          v-model="dateValue"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          size="mini"
        >
        </el-date-picker>

        <span style="margin-left:10px;">打卡内容</span>
        <el-input
          v-model="searchContent"
          placeholder="请输入"
          size="mini"
          class="search"
        ></el-input>

        <el-button-group style="margin-left: 20px">
          <el-button type="primary" size="mini" @click="search">搜索</el-button>
          <el-button size="mini" @click="resetTable">重置</el-button>
        </el-button-group>
      </div>
    </div>

    <!-- <div style="margin-bottom:10px;">
        <span
          >已选择<span style="color: rgb(0, 161, 145); padding: 0 5px">{{
            tableSelection.length
          }}</span
          >项</span
        >
        <el-link
          class="table_link"
          type="danger"
          v-if="tableSelection.length"
          :underline="false"
          @click="handleMultiDelete"
          style="margin: -5px 10px 0px 10px"
          >批量删除</el-link
        >
      </div> -->
    <el-table
      v-loading="loading"
      ref="multipleTable"
      :data="tableData"
      size="small"
      border
      :height="height - 130"
      style="width: 100%; text-align: center; line-height: 18px"
    >
      <!-- <el-table-column
          type="selection"
          width="55"
          align="center"
        ></el-table-column> -->
      <el-table-column
        label="用户"
        align="center"
        prop="username"
        width="150"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="openid"
        align="center"
        prop="openid"
        width="150"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column label="内容" align="center" prop="content" show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        label="图片"
        align="center"
        prop="url"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column label="关联书籍" align="center" width="150" prop="bookId">
      </el-table-column>
      <el-table-column
        label="打卡心情 "
        align="center"
        prop="moodName"
        width="100"
        show-overflow-tooltip
      ></el-table-column>

      <el-table-column
        label="打卡日期"
        align="center"
        prop="date" 
        show-overflow-tooltip
      >
      <template slot-scope="{ row }">
          {{new Date(row.date).toLocaleString()}}
        </template>
    </el-table-column>

      <el-table-column label="操作" align="center" width="100">
        <template slot-scope="{ row }">
        
          <i
            class="el-icon-delete"
            style="margin-left:10px"
            @click="handleDelete(row)"
          ></i>
        </template>
      </el-table-column>
      <template slot="empty">
        <div>
          <i
            class="iconfont iconzanwuziyuan"
            style="font-size: 40px; color: #8c8c8c"
          >
            <p style="font-size: 14px; margin-top: -30px">暂无资源</p>
          </i>
        </div>
      </template>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[20, 100, 150, 200]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      style="float: right; margin-top: 20px"
    >
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: "data_cop",
  components: {},
  props: {
    height: {
      type: Number,
    },
  },
  data() {
    return {
      loading: false,
      currentPage: 1, //当前页码
      pageSize: 20,
      total: 0,
      searchContent: "",
      searchType: "",
      tableData: [],
      moods:[],   
      dateValue: "",
    };
  },
  watch:{
    
  },
  created() {},
  mounted() {   
    this.getData()
   
  },
  methods: {
    getData(){
      this.$reqPost("/community/data",{
        limit:this.pageSize,
        currentPage:this.currentPage,
        searchContent:this.searchContent
      }).then(res=>{
          if(res.code==200){
            this.tableData=res.data
            
          }else{
            this.$message.error(res.msg);
          }
      })
    },
  
    handleDelete(row) {
      this.$confirm("确定删除?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        closeOnClickModal: false,
      }).then(() => {
        let id=row.id;
        this.$reqPost("/community/delData",{
          id
        }).then(res=>{
          if(res.code==200){
            this.getData()
          }else{
            this.$message.error(res.msg);
          }
        })
      });
    },

    handleSizeChange(val) {
      this.pageSize = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },
    handleSelectionChange(val) {
      console.log(val);
    },
    search() {
      this.getData()
    },
    resetTable(){
      this.searchContent=null;
      this.currentPage=1;
      this.pageSize=20;
      this.getData()
    }
  },
};
</script>
<style scoped>
#top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  margin-top: 10px;
  font-size: 14px;
  color: #595959;
}
.search {
  width: 140px;
  margin-left: 10px;
  margin-right: 10px;
}
</style>
